<template>
<!-- 确认收佣弹窗 -->
  <el-dialog
    :close-on-click-modal="false"
    :show-close="false"
    :visible.sync="dialogVisible.trunoff"
    width="30%"
    class="okmssion"
  >
    <div class="okmssion_title">
      {{title}}
    </div>
    <el-form ref="form" class="okmssion" :model="form">
      <el-form-item>
        <textarea class="okmssion_textare" :placeholder="'请添加'+title+'备注'" v-model="form.remark"></textarea>
      </el-form-item>
    </el-form>
    <div class="okmssion_bottom">
      <div style="background-color: #fff; color:#DCDFE6;"  @click="dialogVisible.trunoff=false">取消</div>
      <div style="background-color: #fff; color:#0b8d7e;"  @click="onSubmit">确认</div>
    </div>
  </el-dialog>
</template>
<script>
import {commInvoice,rejectInvoice} from '@/api/business/commission'
import {checkDeptInvoice} from '@/api/business/management'
export default {
  props: {
    dialogVisible: {
      type: Object
    },
    title:{
      type:String
    }
  },
  data() {
    return {
      form: {
        remark:''
      },
      datas:{ //结佣（已审核----------通过）
        invoiceId:'',
        checkStatus:4,//固定
        reason:''
      }
    };
  },
  methods: {
   onSubmit() {
     
      let invoiceId=this.$parent.$parent.invoiceId
      let data={...this.form,invoiceId}
     if(this.title==='拒绝原因'){
       rejectInvoice(data).then(res=>{
         if(res.data.code==='000'){
           this.$message.success('拒绝成功')
              this.colseFn()
         }
          if(res.data.code==='201'){
            this.$message.error(res.data.msg)
            this.colseFn()
          }
       })
     }else if(this.title=="确认收佣"){
            commInvoice(data).then(res=>{
          if(res.data.code==='000'){
            this.$message.success('收佣完成')
              this.colseFn()
          }
          if(res.data.code==='201'){
            this.$message.error(res.data.msg)
            this.colseFn()
          }
        })
     }else if(this.title=='拒绝理由'){
        this.datas.reason=this.form.remark
        this.datas.invoiceId=this.$store.getters.invoiceId
        checkDeptInvoice(this.datas).then(res=>{
          if(res.data.code==='000'){
            this.$message.success('拒绝开票完成')
              this.colseFn()
          }
          if(res.data.code==='201'){
            this.$message.error(res.data.msg)
            this.colseFn()
          }
        })
     }
    },
    colseFn(){
        this.$parent.$parent.onSubmit()
        this.$parent.$parent.handleClick()
        this.dialogVisible.trunoff=false
    }
  }
};
</script>
<style lang="scss" scoped>

 
/deep/.el-dialog {
  position: relative;
  border-radius: 6px;
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 10px 30px;
  }

}
   .okmssion_title{
       text-align: center;
       font-size: 20px;
   }
   .okmssion_textare{
        background-color: #DCDFE6 !important;
        display: inline-block;
        width: 100%;
        font-size: 16px;
        padding: 2px;
        height: 70px;
        border-radius: 4px;
        border: none;
        outline: none;
        margin-top: 10px;
    }
    .okmssion_bottom{
       border-top: 2px solid #DCDFE6;
       display: flex;
       justify-content: space-around;
       width: 100%;
       left: 0;
       position: absolute;
       div{
           height: 40px;
           line-height: 40px;
           width: 50%;
           text-align: center;
           cursor: pointer;
       }
       div:nth-child(1){
           border-right: 1px solid #DCDFE6;
       }
   }
</style>