<template>
  <div class="idetails">
    <!-- 开票详情 -->
    <template v-if="!suCinfoOff&&!upload">
      <!-- 发票详情 -->
      <el-card class="title">
        <img  src="../../../assets/icons/left.png" />
        <span @click="$emit('InvoiceOffFN')">返回收佣列表</span>
        <b>开票详情</b>
      </el-card>
      <!-- context内容 -->
      <div class="scoll_context">
        <el-card style="box-sizing:border-box;" class="box-card herders">
          <div slot="header" class="clearfix">
            <span>{{ideaTop.address}}&nbsp;&nbsp;{{ideaTop.checkDate}}</span>
          </div>
          <el-row>
            <el-col :span="8">
              <div class="lables_title">项目:</div>
              <strong class="rights_text">{{ideaTop.projectName}}</strong>
            </el-col>
            <el-col :span="8">
              <div class="lables_title">开发商抬头:</div> 
              <strong class="rights_text">{{ideaTop.kfsName}}</strong>
              <img
                style="margin-left:10px; cursor: pointer;min-width:20px"
                @click="infoOff.trunoff=true"
                src="../../../assets/icons/alert.png"
              />
            </el-col>
            <el-col :span="8">
             <div class="lables_title">发票类型:</div> 
              <strong class="rights_text">{{ideaTop.type===1?"专票":"普票"}}</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <div class="lables_title">开票总金额:</div>
              <strong class="rights_text" style="color:red">{{ideaTop.money}}￥</strong>
            </el-col>
            <el-col :span="8">
             <div class="lables_title"> 收款公司:</div>
              <strong class="rights_text">{{ideaTop.recCompanyName}}</strong>
            </el-col>
            <el-col :span="8">
             <div class="lables_title">对佣人:</div> 
              <strong class="rights_text">{{ideaTop.standName}}</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
             <div class="lables_title">备注:</div>
              <strong class="rights_text">{{ideaTop.addRemark}}</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="display:flex;align-items: center;" :span="24">
              <div class="lables_title" style="min-width:100px;">操作日志:</div>
              <operation :status="stauts" type="0" />
              <el-button
                @click="oJournalFn"
                style="width:106px;heigth:40px;margin-left:40px;color:#637d93"
              >查看更多</el-button>
            </el-col>
          </el-row>
        </el-card>
        <!-- 客户 -->
        <el-card style="margin-top:20px;">
          <header>认购单列表|{{tableData.length}}</header>
          <el-table
            :cell-style="cellStyle"
            :header-cell-style="{background:'#edfaf8',color:'#333333'}"
            :row-class-name="tableRowClassName"
            :data="tableData"
            style="width: 100%"
          >
            <el-table-column prop="customName" fixed label="客户" width="180"></el-table-column>
            <el-table-column prop="midMobile" label="电话号码" width="180"></el-table-column>
            <el-table-column prop="roomNo" label="房号" width="180"></el-table-column>
            <el-table-column prop="tradeTime" label="认购日期" width="180"></el-table-column>
            <el-table-column prop="sumMoney" label="总房价" width="180">
              <template slot-scope="scope">{{ scope.row.sumMoney }}元</template>
            </el-table-column>
            <el-table-column prop="sumComission" label="总佣金" width="180">
              <template slot-scope="scope">{{ scope.row.sumComission }}元</template>
            </el-table-column>
            <el-table-column prop="haspay" label="已收佣" width="180">
              <template slot-scope="scope">{{ scope.row.haspay }}元</template>
            </el-table-column>
            <el-table-column prop="unpay" label="未收佣" width="180">
              <template slot-scope="scope">{{ scope.row.unpay }}元</template>
            </el-table-column>
            <el-table-column prop="deptUnbilled" label="未开票" width="180">
                  <template slot-scope="scope">{{ scope.row.deptUnbilled }}元</template>

            </el-table-column>
            <el-table-column prop="billMoney" label="开票金额" width="180">
              <template slot-scope="scope">{{ scope.row.billMoney }}元</template>
            </el-table-column>
            <el-table-column prop="inputCanCommission" label="本次可结" width="180">
              <template slot-scope="scope">{{ scope.row.inputCanCommission }}元</template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看成交单</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div>
            <Pagination
              @handleSizeChange="handleSizeChange"
              @handleCurrentChange="handleCurrentChange"
              :paginationData="paginationData"
            />
          </div>
        </el-card>
        <!-- 客户 -->

        <!-- 开票信息 -->
        <el-card style="margin-top:20px;" v-if="stauts!=3||stauts!=4">
          <template>
          <div v-if="stauts==5||stauts==6||stauts==7" slot="header">
            <span>开票信息</span>
          </div>
          <el-form
            :model="ideaTop"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          > 
            <el-form-item v-if="stauts==5||stauts==6" label="开票凭证:" prop="desc">
              <!-- <el-input type="textarea" v-model="ruleForm.desc"></el-input> -->
              <template v-if="ideaTop.picUrl">
                <img style="width:100px;heigth:80px;margin-right:10px;" v-for="(item,index) in ideaTop.picUrl" :key="index" :src="item" />
              </template>
            </el-form-item>
            <el-form-item v-if="stauts==5||stauts==7||stauts==6" label="开票备注:">
              <el-input type="textarea" v-model="ideaTop.remark"></el-input>
            </el-form-item>
            <el-form-item v-if="stauts==6" label="收佣备注:">
              <el-input type="textarea" v-model="ideaTop.doneRemark"></el-input>
            </el-form-item>
          </el-form>
          <!-- 审核 -->
          <div v-if="stauts==2&&$store.getters.userinfo.role==4"  class="miss_down">
            <div @click="examineFn(2)">
              <img src="../../../assets/icons/shenghe.png" />
              审核
            </div>
          </div>
          <!-- 审核 -->
          <!-- 保存 -->
          <div v-if="ideaTop.addName===$store.getters.userinfo.realName&&(stauts==1||(stauts==4&&$store.getters.userinfo.role!=8))" class="miss_downsave">
            <div >
                <el-button type="danger" @click="addProEidt" style="background:#f1420ef7;" icon="el-icon-edit">编辑</el-button>
                <el-button type="primary" v-if="stauts==1" style="background:#0b8d7e;" @click="examineFn(1)" icon="el-icon-check">提交</el-button>
            </div>
          </div>
          <!-- 保存 -->
          <!-- 确认收佣 -->
           <div v-if="stauts==5&&$store.getters.userinfo.role==8"  class="miss_down">
            <div @click="examineFn(5)">
              <img src="../../../assets/icons/确认收佣icon.png" />
              确认收佣
            </div>
          </div>
          <!-- 确认收佣 -->
          <!-- 财务审核 -->
          <div v-if="stauts==3&&$store.getters.userinfo.role==8" class="miss_downsave">
            <div >
                <el-button type="danger" @click="Refuse" style="background:#f1420ef7;" icon="el-icon-circle-close">拒绝开票</el-button>
                <el-button type="primary"  style="background:#0b8d7e;" @click.native="upload=true" icon="el-icon-edit-outline">去开票</el-button>
            </div>
          </div>
          <!-- 财务审核 -->
         
          </template>
        </el-card>
        <!-- 开票信息 -->
      </div>
      <!-- context内容 -->

      <!-- 操作日志弹窗 -->
      <oJournal title="操作日志" :status="stauts" :dialogVisible="oJournalOff" />
      <!-- 操作日志弹窗 -->
      <!-- 审核弹窗 -->
      <examine :dialogVisible="examineOff" />
      <!-- 审核弹窗 -->
      <!-- 开发商弹窗 -->
      <oJournal title="开发商发票抬头" :dialogVisible="infoOff" />
      <!-- 开发商弹窗 -->
        <!-- 确认收佣弹窗 -->
      <okcomssion title="确认收佣" :dialogVisible="okcomssion" />
      <!-- 确认收佣弹窗 -->
      <!-- 拒绝开票弹窗 -->
      <okcomssion title="拒绝原因" :dialogVisible="RefuseData" />
      <!-- 拒绝开票弹窗 -->
    
      
    </template>
    <!-- ////////////////////////////////// -->
    <!-- 成交单详情 -->
    <suCinfo type="shouyong" :suCinfoAll="suCinfoAll" @InvoiceOffFN="suCinfoOff=false" v-else-if="suCinfoOff" />
    <!-- 成交单详情 -->
    <!-- 上传开票信息 -->
    <upload :InvoiceDatas="InvoiceData" @InvoiceOffFN="upload=false" v-else-if="upload"/>
    <!-- 上传开票信息 -->
  </div>
</template>
<script>
// import suCinfo from "./suCinfo";
import oJournal from "components/common/oJournal";
import suCinfo from "components/common/suCinfo";
import okcomssion from "./okcomssion";
import examine from "./examine";
import Upload from './Upload'
import {ticketKfsDetail,commitInvoice,addInvoice} from '@/api/business/commission'
import myMixin from '@/mixin/index'
import { dateTime } from "@/utils/common";
export default {
  mixins:[myMixin],
  components: {
    oJournal,
    examine,
    suCinfo,
    okcomssion,
   'upload':Upload
  },
  data() {
    return {
      title: "金水湾   2019-9-23",
      ideaTop: {
        address: "",
        checkDate: "",
        projectName: "",
        type: "", //发票类型 1：专票 2：普票
        money: "",
        recCompanyName: "",
        addName:'',//操作人管理权限
        standName: "",
        remark: "saaaaaaaaaa", //开票操作或拒绝开票的备注
        kfsName: "",
        addRemark: "",
        picUrl: null, //开票操作的凭证图片数组
        doneRemark: "" //确认收佣的备注
      },
      tableData: [],
      paginationData: {
        //分页
        pageSize: 10,
        currentPage: 1,
        total: 1000
      },
      oJournalOff: {
        //操作日志弹窗数据
        trunoff: false,
        data: null
      },
      ruleForm: {
        // desc: this.ideaTop.doneRemark
      },
      rules: {
        desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      },
      examineOff: {
        //审核弹窗
        trunoff: false,
        data: null
      },
      infoOff: {
        //开发商弹窗
        trunoff: false,
        data: {
          kfsName: "",
          dutyNo: "",
          bankName: "",
          mobile: "",
          address: "",
          account: ""
        }
      },
      okcomssion:{
        trunoff: false,
      },
      RefuseData:{
         trunoff:false
      },
      stauts: null, //收佣状态
      // 提交参数
      fromdats:{
        projectId: null,
        type: "",
        dutyNo: "",
        recCompanyId: "",
        standUid: "",
        remark: "",
        details:''
      },
      // 成交单详情
      suCinfoOff: false,
      suCinfoAll:null,
      ticketId:null,
      // 成交单详情
      // 上传开票
      upload:false
    };
  },
  props: {
    InvoiceData: {
      type: Object
    },
    InvoiceInfo: {
      type: Array
    }
  },
  methods: {
    // 查看更多
    oJournalFn() {
      this.oJournalOff.trunoff = true;
    },
    // 审核//提交//确认收佣//上传开票
    examineFn(stauts) {
      switch(stauts){
         case 2:
            this.examineOff.trunoff = true;
            break;
         case 1:
           for(let key in this.fromdats){
             this.fromdats[key]=this.InvoiceData[key]
           }
           this.fromdats.details=this.fromdats.details.map(item=>{
              let obj = {};
               obj.ticketId = item.ticketId;
               obj.billMoney = item.billMoney;
               obj.canCommission = item.canCommission;
               return obj;
            })
            this.$confirm("提交后不可编辑，是否提交?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
              })
              .then(()=>{
                  commitInvoice({ invoiceId: this.$parent.invoiceId }).then(ress=>{
                     if(ress.data.code==='000'){
                       this.$message.success('提交成功')
                       this.$parent.handleClick()
                     }
                 })
              })
              .catch(() => {
                  this.$message({
                    type: "info",
                    message: "已取消"
                  });
                  // this.$parent.onSubmit()
                });

            
          //  addInvoice(this.fromdats).then(res=>{
          //        commitInvoice({ invoiceId: res.data.bizData.invoiceId }).then(ress=>{
          //            if(ress.data.code==='000'){
          //              this.$message.success('提交成功')
          //              this.$parent.onSubmit()
          //            }
          //        })
          //  })
            //  this.$confirm("是否提交?", "提示", {
            //     confirmButtonText: "确定",
            //     cancelButtonText: "取消",
            //     type: "warning"
            //   })
            //     .then(() => {
            //       // 提交
            //       commitInvoice({ invoiceId: res.data.bizData.invoiceId }).then(
            //         res => {
            //           if (res.data.code === "000") {
            //             this.$message({
            //               type: "success",
            //               message: "提交成功!"
            //             });
            //           }
            //         this.$parent.onSubmit()
            //         }
            //       );
            //     })
            //     .catch(() => {
            //       this.$message({
            //         type: "info",
            //         message: "已保存"
            //       });
            //       this.$parent.onSubmit()
            //     });
            break;
          case 5:
            this.okcomssion.trunoff=true
            break;
         


           
      }
    },
    // 编辑
    addProEidt(){
         this.$parent.eidtPro()
         this.$parent.$refs.eidts.hhghjgl(this.InvoiceData)
    },
    handleSizeChange() {},
    handleCurrentChange() {},
    // 成交单详情
    handleClick(val) {
       this.suCinfoOff = true;
       this.ticketId=val.ticketId
       ticketKfsDetail({ticketId:this.ticketId}).then(res=>{
        if(res.data.code==='000'){
          this.suCinfoAll=res.data.bizData
        }
      })
    },
    // tableRowClassName({ row, rowIndex }) {
    //   //操作表格隔行颜色
    //   if (rowIndex % 2) {
    //     return "warning-row";
    //   } else {
    //     return "success-row";
    //   }
    //   return "";
    // },
    // cellStyle({ row, column, rowIndex, columnIndex }) {
    //   if (columnIndex === 10) {
    //     // 指定列号
    //     return "color:red";
    //   } else {
    //     return "";
    //   }
    // },
    // 数据赋值操作
    handlerData() {
      let data = this.InvoiceData;
      this.stauts = this.$parent.status.toString();
      this.tableData = data.details.map(item=>{
        item.tradeTime=dateTime(item.tradeTime)
        return item
      });
      this.paginationData.total = this.tableData.length;
      let idea = this.ideaTop;
      for (let key in idea) {
        idea[key] = data[key];
      }
      let copy = this.infoOff.data;
      for (let key in copy) {
        copy[key] = data[key];
      }
    },
    //拒绝开票
    Refuse(){
        this.RefuseData.trunoff=true
    },
  },
  watch: {
    InvoiceData: {
      handler(val, old) {
        this.handlerData();
      },
      deep: true
    },
    InvoiceInfo: {
      handler(val, old) {
        this.oJournalOff.data = this.InvoiceInfo;
      },
      deep: true
    }
  }
};
</script>
<style lang="scss">
@import '@/css/common.scss';
.idetails {
  
  .scoll_context {
    height: calc(100vh - 200px);
    overflow-y: auto;
    margin-top: 20px;
    .lables_title{
      min-width: 100px;
      max-width: 100px;
      text-align: right;
    }
    .el-col-24{
      display: flex;
    }
    .el-col-8{
      display: flex;
    }
    .rights_text {
      margin-left: 20px;
      font-weight: normal;
    }
    .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 140px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
    .miss_downsave{
       display: flex;
       justify-content: center;
    }
   
  }
}
.herders {
  .el-row {
    margin-bottom: 20px;
  }
  width: 100%;
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }
}
.el-card .box-card .herders .is-always-shadow {
  box-sizing: border-box;
}
</style>