<template>
  <div class="suCinfo">
    <template v-if="!tranSinfo">
      <!-- 成交单详情 -->
      <el-card class="title">
        <img src="../../assets/icons/left.png" />
        <span @click="$emit('InvoiceOffFN')">返回开票详情</span>
        <b>成交单详情</b>
      </el-card>
      <!-- context内容 -->
      <div class="scoll_context">
        <el-card style="box-sizing:border-box;" class="box-card herders">
          <div slot="header" class="clearfix">
            <span>{{ideaTop.customerName}}</span>
            <strong>{{ideaTop.customerPhone}}</strong>
            <el-button
              @click="oJournalFn"
              style=" float:right;;width:106px;heigth:40px;margin-left:40px;color:#637d93"
            >查看详情</el-button>
          </div>
          <el-row>
            <el-col :span="8">
            <div style="min-width:100px;text-align: right;"> 项目:</div> 
              <strong class="rights_text">{{ideaTop.projectName}}</strong>
            </el-col>
            
            <el-col :span="8">
            <div style="min-width:100px;text-align: right;"> 门店:</div> 
              <strong class="rights_text">{{ideaTop.deptName}}</strong>
            </el-col>
             <el-col :span="8">
             <div style="min-width:100px;text-align: right;">总佣金:</div> 
              <strong class="rights_text" style="color:red">{{ideaTop.sumCommission}}￥</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">已收佣金:</div>
              <strong class="rights_text">{{ideaTop.haspay}}</strong>
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">未收佣金:</div>
              <strong class="rights_text">{{ideaTop.unpay}}</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="display:flex;align-items: center;" :span="24"></el-col>
          </el-row>
        </el-card>
        <!-- 收佣记录 -->
        <el-card style="margin-top:20px;padding:8px 18px;">
          <header style="font-weight: 800;padding-bottom: 10px;">收佣记录</header>
          <el-table
            :cell-style="cellStyle"
            :header-cell-style="{background:'#edfaf8',color:'#333333'}"
            :row-class-name="tableRowClassName"
            :data="tableData"
            style="width: 100%"
          >
            <el-table-column :prop="type=='mendian'?'addName':'standName'" fixed label="对佣人" width="180"></el-table-column>
            <el-table-column prop="billMoney" label="本次收佣金额" width="180">
              <template slot-scope="scope">{{ scope.row.billMoney }}元</template>
            </el-table-column>
            <el-table-column  prop="inputCanCommission" label="对外本次可结">
              <template slot-scope="scope">{{ scope.row.inputCanCommission==null?0:scope.row.inputCanCommission}}元</template>
            </el-table-column>
            <el-table-column prop="status" label="状态" width="180">
              <!-- <template slot-scope="scope">
                  {{scope}}
              </template>-->
              <template
                slot-scope="scope"
              >{{ scope.row.status===2?'待审核': scope.row.status===3?'审核通过':scope.row.status=== 5?'开票':'已收佣'}}</template>
            </el-table-column>
            <el-table-column prop="time" label="时间" width="180"></el-table-column>
            <el-table-column fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div>
            <Pagination
              @handleSizeChange="handleSizeChange"
              @handleCurrentChange="handleCurrentChange"
              :paginationData="paginationData"
            />
          </div>
        </el-card>
        <!-- 收佣记录 -->
      </div>
      <!-- context内容 -->
    </template>
    <infoSuccess type="业绩单详细" @InvoiceOffFN="tranSinfo=false" :infoSuObj="infoSuObj" v-if="tranSinfo" />
  </div>
</template>
<script>
import { ticketDetail } from "@/api/business/commission";
import { dateTime } from "@/utils/common";
import infoSuccess from "components/common/infoSuccess";
export default {
  components: {
    infoSuccess
  },
  data() {
    return {
      ideaTop: {
        customerPhone:'',
        projectId: "", //项目ID
        projectName: "", //项目名
        customerName: "", //客户姓名
        customerPhone: "", //客户号码
        deptId: "", //门店ID
        deptName: "", //门店名称
        sumCommission: "", //开发商总佣金
        unbilled: "", //未开票金额
        unpay: "", //未收佣金
        haspay: "" //已收佣金
      },
      tableData: [
        // {
        //   invoiceId: 3, //发票ID，团购费时为0
        //   billMoney: "10000", //本次收佣金额（团购费时为团购费金额）
        //   inputCanCommission: "9000", //本次输入的可结金额（团购费时为团购费可结金额）
        //   status: 2, //状态 2 :待审核 3:审核通过 5开票 6已收佣
        //   time: "2019-11-19", //对应状态的时间，团购费为业绩单确认时间
        //   standUid: "XXXX", //对佣人ID（团购费为成交单添加人ID）
        //   standName: "张三丰" //对佣人姓名（团购费为成交单添加人姓名）
        // }
      ],
      paginationData: {
        //分页
        pageSize: 10,
        currentPage: 1,
        total: 1000
      },
      tranSinfo: false,
      infoSuObj: null //详情数据
    };
  },
  props: {
    suCinfoAll: {
      type: Object
    },
    type:{  //判断对佣人字段不同的
      type:String
    }
  },
  methods: {
    oJournalFn() {
      this.tranSinfo = !this.tranSinfo;
    },
    handleSizeChange() {},
    handleCurrentChange() {},
    infoDelaFn() {
      let ticketId = this.$parent.ticketId;
      ticketDetail({ ticketId }).then(res => {
        console.log(res);
        if (res.data.code === "000") {
          this.infoSuObj = res.data.bizData;
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      //操作表格隔行颜色
      if ((rowIndex = 0)) {
        return "warning-row";
      } else {
        return "success-row";
      }
      return "";
    },
    cellStyle({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 10) {
        // 指定列号
        return "color:red";
      } else {
        return "";
      }
    },
    handleClick(val) {
      console.log(val);
      this.$parent.suCinfoOff = false;
      this.$parent.$parent.handleClick(val);
    },
    // 数据赋值操作
    handlerData() {
      this.tableData = this.suCinfoAll.records.map(item => {
        item.time = dateTime(item.time);
        return item;
      });
      this.infoDelaFn();
      let data = this.ideaTop;
      for (let key in data) {
        data[key] = this.suCinfoAll[key];
      }
      this.paginationData.total = this.tableData.length;
    }
  },
  mounted() {
    //
  },
  watch: {
    suCinfoAll: {
      handler(val, old) {
        this.handlerData();
      },
      deep: true
    }
  }
};
</script>
<style lang="scss">
@import '@/css/common.scss';
.suCinfo {
  .clearfix {
    font-weight: 900;
    box-sizing: border-box;
    position: relative;
    height: 39px;
    line-height: 39px;
    strong{
      padding-left: 30px;
    }
    
   
  }
  
  .scoll_context {
    height: calc(100vh - 200px);
    overflow-y: auto;
    margin-top: 20px;
    .rights_text {
      margin-left: 20px;
      font-weight: normal;
    }
    .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 120px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
    .el-table .warning-row {
      background-color: #edfaf8;
    }

    .el-table .success-row {
      background: #fafafa;
    }
  }

  .cards-enter-active,
  .cards-leave-active {
    transition: opacity 0.5s;
  }
  .cards-enter, .cards-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
}
.herders {
  .el-row {
    margin-bottom: 20px;
  }
  width: 100%;
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }
}
.el-card .box-card .herders .is-always-shadow {
  box-sizing: border-box;
}
</style>