<template>
  <el-dialog
    :close-on-click-modal="false"
    :show-close="false"
    :visible.sync="dialogVisible.trunoff"
    width="600px"
  >
    <div class="top">
      <img src="../../assets/icons/绿色竖线.png" />
      <span>{{title}}</span>
    </div>
    <img class="line_cat" src="../../assets/icons/操作日志页面分割线.png" />
    <template v-if="title==='操作日志'">
      <div class="operation">
        <operation :status="status" type="1"/>
      </div>
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in dialogVisible.data"
          :key="index"
          :color="activity.color"
        >
          <div>{{type[activity.opType-1]}}</div>
          <div style="padding-top:8px">
            <div style="display:flex;">
              <div>{{activity.opUserName}}</div>
              <div style="padding-left:15px;">{{activity.opTime}}</div>
            </div>
          </div>
        </el-timeline-item>
      </el-timeline>
      
    </template>
    <template v-if="title==='开发商发票抬头'||title==='发票抬头'">
      <div class="line_content">
        <div>发票抬头:</div>
        <div v-if="title==='发票抬头'">{{dialogVisible.data.name}}</div>
        <div v-if="title==='开发商发票抬头'">{{dialogVisible.data.kfsName}}</div>
      </div>
      <div class="line_content">
        <div>税号:</div>
        <div>{{dialogVisible.data.dutyNo}}</div>
      </div>
      <div class="line_content">
        <div>开户银行:</div>
        <div>{{dialogVisible.data.bankName}}</div>
      </div>
      <div class="line_content">
        <div>银行账号:</div>
        <div>{{dialogVisible.data.account}}</div>
      </div>
      <div class="line_content">
        <div>单位电话:</div>
        <div>{{dialogVisible.data.mobile}}</div>
      </div>
       <div class="line_content">
        <div>单位地址:</div>
        <div>{{dialogVisible.data.address}}</div>
      </div>
    </template>
    <template v-if="title==='账户'">
      <div class="custer_content">
        <div class="nav">
        <div>{{dialogVisible.data.bankName}}</div>
        <div>{{dialogVisible.data.accName}}</div>
        </div>
        <div class="number">{{dialogVisible.data.accountId}}</div>
      </div>
   
    </template>
     <div class="colse_bottom">
        <el-button @click="colseFn" class="btn">关闭</el-button>
      </div>
  </el-dialog>
</template>
<script>
import operation from "./operation";
export default {
  components: {
    operation
  },
  data() {
    return {
      activities: [
        {
          opUserName: "支持自定义颜色",
          opTime: "2018-04-12 20:46",
          color: "#0bbd87",
          opType:'2'
        },
        {
          opUserName: "支持自定义颜色",
          opTime: "2018-04-03 20:46",
          color: "#0bbd87",
          opType:'2'
        },
        {
          opUserName: "支持自定义尺寸",
          opTime: "2018-04-03 20:46",
          color: "#0bbd87",
          opType:'2'

        },
        {
          opUserName: "默认样式的节点",
          opTime: "2018-04-03 20:46",
          color: "#0bbd87",
          opType:'2'
        }
      ],
      infoIt:{
          kfsName:'',
          dutyNo:'',
          bankName:'',
          mobile:'',
          address:'',
          account:''
      },
      type:['新增','编辑','提交','审核通过','审核拒绝','开票','收佣','拒绝开票']
    };
  },
  props: {
    dialogVisible: {
      type: Object
    },
    title: {
      type: String
    },
    status:{
      type:String
    }
  },
  methods: {
    colseFn() {
      this.dialogVisible.trunoff = false;
    }
  },
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  border-radius: 14px;
  box-sizing: border-box;
  position: relative;
  
  .top {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: #333333;
    img {
      margin-right: 10px;
    }
    position: relative;
    margin-bottom: 60px;
  }
  .operation {
    margin-bottom: 30px;
    text-align: center;
    width: 100%;
  }
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 30px 77px;
  }
  .line_cat {
    position: absolute;
    width: 100%;
    left: 0;
    top: 80px;
  }
  .colse_bottom {
    width: 100%;
    text-align: center;
    margin-top: 16px;
    .btn {
      background-color: #0b8d7e;
      color: #fff;
      font-size: 24px;
    }
  }
  .line_content{
    display: flex;
    padding: 10px 0;
    & div:nth-child(1){
      min-width: 130px;
      text-align: right;
    }
    & div:nth-child(2){
      padding-left: 26px;
    }
  }
  .custer_content{
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    padding-left: 60px;
    .nav{
      div:nth-child(1){
        color: #000;
      }
      div:nth-child(2){
        color: rgb(196, 194, 194);
      }
    }
    .number{
      padding-top: 10px;
      font-size: 26px;
      font-weight:800;
    }
  }
  .el-timeline{
    margin-left: 30px;
  }
}
</style>