<template>
  <div class="upload">
    <!-- 上传开票信息 -->
    <el-card class="title">
      <img src="../../../assets/icons/left.png" />
      <span @click="$emit('InvoiceOffFN')">返回开票详情</span>
      <b>上传开票信息</b>
    </el-card>
    <div class="scoll_context">
      <el-card>
        <div slot="header" class="clearfix">
          <span>{{InvoiceDatas.projectName}}&nbsp;&nbsp;{{InvoiceDatas.addDate}}</span>
        </div>
        <div class="kfsuser">
          <div>开票总金额:<span style="color:red">￥{{InvoiceDatas.money}}</span></div>
          <div>
            开发商抬头:{{InvoiceDatas.kfsName}}
            <img
              style="margin-left:10px; cursor: pointer;"
              @click="infoOff.trunoff=true"
              src="../../../assets/icons/alert.png"
            />
          </div>
          <div>对佣人:{{InvoiceDatas.standName}}</div>
        </div>
        <el-form :rules="rules" :model="formInline">
          <el-form-item prop="deliverUid" label="派送人:">
            <el-autocomplete
                     class="inline-input"
                     style="width:90%;"
                     v-model="values"
                     :fetch-suggestions="standUidSearch"
                     placeholder="请输入对佣人"
                     @select="handleSelectstandUid"
                     @keydown.native="keydownFnss"
                   ></el-autocomplete>
          </el-form-item>
          <el-form-item prop label="开票凭证:">
            <!-- <el-upload
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt />
            </el-dialog>-->
            <!-- <div class="yulan">
            <img v-for="(item) in urls" :key="item" class="upimg" :src="item"/>
            <div class="head-img">
              <img src="../../../assets/icons/上传icon.png" @click="upLoad" id="my-img" />
              <input type="file" id="img-upload" />
            </div>
           </div> -->
           <upPictrue/>
          </el-form-item>
          <el-form-item prop="remark" label="对佣人:">
            <el-input
              style="width:90%;"
              type="textarea"
              v-model="formInline.remark"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="miss_down">
          <div @click="submit">
            <img src="../../../assets/icons/审核icon.png" />
            提交
          </div>
        </div>
      </el-card>
    </div>
    <!-- 开发商弹窗 -->
    <oJournal title="开发商发票抬头" :dialogVisible="infoOff" />
    <!-- 开发商弹窗 -->
  </div>
</template>
<script>
import {
  userList,
  openInvoice
} from "@/api/business/commission";
import oJournal from "components/common/oJournal";
import upPictrue from '../../common/upPictrue'
export default {
  components: {
    oJournal,
    upPictrue
  },
  data() {
    return {
      urls:[],
      values:'',
      formInline: {
        remark: "",
        deliverUid: "",
        picUrl: [],
        invoiceId: ""
      },
      rules: {
        deliverUid: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        remark: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      },
      infoOff: {
        //开发商弹窗
        trunoff: false,
        data: {
          kfsName: "",
          dutyNo: "",
          bankName: "",
          mobile: "",
          address: "",
          account: ""
        }
      },
       selectstandUid: {
        //对佣人搜索条件
        pageSize: 40,
        pageNo: 1,
        key: "",
        deptId: ""
      },
      selectItemstandUid:null
    };
  },
  props: {
    InvoiceDatas: {
      type: Object
    }
  },
  methods: {
    submit() {
      this.formInline.invoiceId = this.$parent.$parent.invoiceId;
      this.formInline.picUrl=this.urls
      openInvoice(this.formInline).then(res=>{
        console.log(res)
      })
    },
    upLoad(){
      let imgs=document.getElementById('img-upload')
      imgs.click()
      var that=this
      imgs.onchange=function(e){
        //创建读取文件的对象
        const reader = new FileReader();
        //使用该对象读取file文件
        reader.readAsDataURL(e.target.files[0]);
        //读取文件成功后执行的方法函数
        reader.onload = function(e) {
          //读取成功后返回的一个参数e，整个的一个进度事件
          //选择所要显示图片的img，要赋值给img的src就是e中target下result里面
          //的base64编码格式的地址
          console.log( e.target.result)
          that.urls.push(e.target.result) ;
        };
      }
      
    },
    handlerData() {},
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      console.log(file);
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 搜索条件派送人
    async standUidSearch(queryString, cb) {
      this.selectstandUid.key = queryString;
      await this.requestSearchTwo(cb);
    },
    handleSelectstandUid(val) {
      console.log(val)

      this.formInline.deliverUid = val.uid;
      this.selectItemstandUid = val;
    },
    keydownFnss() {
      if (this.values.length === 0) {
        this.formInline.standUid = "";
        this.selectstandUid.key = "";
      }
    },
    requestSearchTwo(cb) {
      this.selectstandUid.deptId = this.$store.getters.userinfo.departmentId;
      userList(this.selectstandUid).then(res => {
        // console.log(res)
        if (res.data.code === "000") {
          if (res.data.bizData.items.length === 0) {
            return cb([{}]);
          }
          this.standUidArr = res.data.bizData.items.map(item => {
            item.value = item.name;
            return item;
          });
          var restaurants = this.standUidArr;
          return cb(restaurants);
        }
      });
    },
    // 搜索条件派送人
  },
  mounted() {
    //  console.log('08888888888888888888888')
    let data = this.infoOff.data;
    let copyinfo = this.InvoiceDatas;
    for (let key in data) {
      data[key] = copyinfo[key];
    }
    this.infoOff.data = data;
  },
  watch: {
    InvoiceDatas: {
      handler(val, old) {
        this.handlerData();
      },
      deep: true
    }
  }
};
</script>
<style lang="scss">
@import '@/css/common.scss';
.upload {
  margin-top: 20px;
 
  .scoll_context {
    height: calc(100vh - 200px);
    overflow-y: auto;
    margin-top: 20px;
    .kfsuser {
      display: flex;
      padding: 0 10px;
      justify-content: space-between;
      height: 40px;
      align-items: center;
      border: 1px solid rgb(110, 188, 224);
      border-radius: 6px;
      margin-bottom: 20px;
      background-color: rgb(217, 232, 240);
    }
    .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 140px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
   #img-upload{
    display: none;
   }
   .yulan{
     display: flex;
   }
   .upimg{
     display: inline-block;
     width: 120px;
     height: 130px;
   }
   
  }
}
</style>