<template>
  <div>
    <!-- 编辑收佣 -->
    <el-dialog
      title="编辑收佣"
      :visible.sync="dialogVisible.trunoff"
      width="80%"
      :before-close="handleClose"
      class="addPro"
      top="20"
    >
      <el-card>
        <div slot="header" class="clearfix">
          <span>项目信息</span>
        </div>
        <!-- <el-button @click="addFn" class="btn">添加</el-button> -->
        <el-form
          :label-position="labelPosition"
          label-width="100px"
          :model="formInline"
          class="demo-form-inline"
          :rules="rules"
          ref="ruleForm"
        >
          <el-row>
            <el-col :span="8">
              <el-form-item prop="projectId" label="项目">
                <el-select
                  style="width:90%;"
                  disabled
                  filterable
                  v-model="formInline.projectId"
                  placeholder="请选择项目"
                >
                  <el-option
                    v-for="(item,index) in dataSearch"
                    :key="item.projectId+index+item.projectName"
                    :label="item.projectName"
                    :value="item.projectId"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="dutyNo" label="开发商账户">
                <el-input
                  style="width:90%;"
                  @focus="todutyNo"
                  placeholder="请输入开发商账户"
                  v-model="formInline.dutyNo"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="recCompanyId" label="收款公司">
                <el-select
                  style="width:90%;"
                  v-model="formInline.recCompanyId"
                  placeholder="请选择收款公司"
                >
                  <el-option :label="item.name" :value="item.companyId" v-for="(item,index) in $store.getters.companyList" :key="index"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="type" label="发票类型">
                <el-select style="width:90%;" v-model="formInline.type" placeholder="请选择发票类型">
                  <el-option label="专票" :value="1"></el-option>
                  <el-option label="普票" :value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="standUid" label="对佣人">
                <!-- <el-select
                    style="width:90%;"
                    @keydown.native="selectkeydowns"
                    @focus="selectkeydowns"
                    @change="changeStandUid"
                    v-model="formInline.standUid"
                    placeholder="请选择对佣人"
                  >
                    <el-option
                      v-for="(item,index) in standUidArr"
                      :key="item.uid+index+item.value"
                      :label="item.value"
                      :value="item.uid"
                    ></el-option>
                </el-select>-->
                <el-autocomplete
                  class="inline-input"
                  style="width:90%;"
                  v-model="values"
                  :fetch-suggestions="standUidSearch"
                  placeholder="请输入对佣人"
                  @select="handleSelectstandUid"
                  @keydown.native="keydownFnss"
                ></el-autocomplete>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="备注">
                <el-input
                  style="width:90%;"
                  type="textarea"
                  v-model="formInline.remark"
                  placeholder="请输入备注"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
      <i @click="addFn" class="el-icon-plus addIcon">添加认购单</i>
      <!-- 认购单列表  -->
      <el-card v-if="tableDataFn">
        <div style="color:#000;font-size:22px;padding-bottom:8px;">认购单列表|{{SubscriptionArr.length}}</div>
        <el-table v-if="tableDataFn" :data="tableDataFn" border height="250" style="width: 100%;">
          <el-table-column fixed prop="customName" label="姓名" width="120"></el-table-column>
          <el-table-column prop="preMobile" label="电话号码"></el-table-column>
          <el-table-column prop="roomNo" label="房号"></el-table-column>
          <el-table-column label="认购日期" width="220">
            <template slot-scope="scope">{{ scope.row.tradeTime }}</template>
          </el-table-column>
          <el-table-column prop="sumMoney" label="认购金额">
            <template slot-scope="scope">{{ scope.row.sumMoney }}元</template>
          </el-table-column>
          <el-table-column prop="sumComission" label="总佣金">
            <template slot-scope="scope">{{ scope.row.sumComission }}元</template>
          </el-table-column>
          <el-table-column prop="haspay" label="已收佣">
            <template slot-scope="scope">{{ scope.row.haspay }}元</template>
          </el-table-column>
          <el-table-column prop="unpay" label="未收佣">
            <template slot-scope="scope">{{ scope.row.unpay }}元</template>
          </el-table-column>
          <el-table-column prop="unbilled" label="未开票">
            <template slot-scope="scope">{{ scope.row.unbilled }}元</template>
          </el-table-column>
          <!-- <el-table-column prop="inputCanCommission" label="可结金额"></el-table-column> -->
          <el-table-column prop="billMoney" fixed="right" label="开票金额" width="120">
            <template slot-scope="scope">
              <el-input v-model.number="scope.row.billMoney"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="inputCanCommission" fixed="right" label="对外本次可结" width="120">
            <template slot-scope="scope">
              <el-input v-model.number="scope.row.inputCanCommission"></el-input>
              <!-- <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
              <el-button type="text" size="small">编辑</el-button>-->
            </template>
          </el-table-column>
          <el-table-column fixed="right" align="center" label="操作" width="100">
            <template slot-scope="scope">
              <i style="cursor: pointer;" @click="ClickDelet(scope.row)" class="el-icon-delete"></i>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!-- 认购单列表  -->
      <!-- 开票总金额 -->
      <el-card class="dialog-footer">
        <!-- <el-button @click="resetForm()">取 消</el-button> -->
        <div>
          开票总金额 :
          <span style="color:red">￥{{totolFn}}</span>
        </div>
        <el-button class="saveColor" type="primary" @click="onSubmit">保存</el-button>
      </el-card>
      <!-- 开票总金额 -->
    </el-dialog>
    <!-- 开发商账户 -->
    <toDycard ref="toDycard" :dialogVisible="dataDotoDy" />
    <!-- 开发商账户 -->
    <!-- 认购单弹窗 -->
    <Subscription
      @handleSelectionChange="handleSelectionChange"
      ref="Subscription"
      :dialogVisible="SubscriptionD"
    />
    <!-- 认购单弹窗 -->
  </div>
</template>
<script>
import {
  invoiceProject,
  userList,
  deptInvoiceDetail,
  addInvoice,
  commitInvoice,
  editInvoice
} from "@/api/business/commission";
import toDycard from "./toDycard";
import Subscription from "./Subscription";
export default {
  components: {
    toDycard,
    Subscription
  },
  props: {
    dialogVisible: {
      type: Object
    }
  },
  data() {
    return {
      values: "",
      //   dialogVisible: false
      rules: {
        projectId: [{ required: true, message: "请输入项目", trigger: "blur" }],
        dutyNo: [
          { required: true, message: "请输入开发商账户", trigger: "blur" }
        ],
        recCompanyId: [
          { required: true, message: "请选择收款公司", trigger: "blur" }
        ],
        type: [{ required: true, message: "请选择发票类型", trigger: "blur" }],
        standUid: [{ required: true, message: "请输入对佣人", trigger: "blur" }]
      },
      labelPosition: "center",
      dataSearch: [],
      standUidArr: [],
      formInline: {
        //确定提交参数
        projectId: null,
        type: "",
        dutyNo: "",
        recCompanyId: "",
        standUid: "",
        remark: ""
      },
      dataDotoDy: {
        //开发商户详情
        trunoff: false,
        projectName: "项目上",
        dutyNo: "",
        projectId: ""
      },
      // 项目搜索条件传参
      value: "",
      values: "",
      idetails: false,
      // 项目搜索条件传参
      selectchild: {
        //项目搜索条件传参
        pageNo: 1,
        pageSize: 10,
        platformId: this.$store.getters.userinfo.departmentId,
        status: 0,
        keyName: ""
      },
      selectstandUid: {
        //对佣人搜索条件
        pageSize: 40,
        pageNo: 1,
        key: "",
        deptId: ""
      },
      selectItem: null, //项目选择
      selectItemstandUid: null, //对佣人选择
      // 认购单
      SubscriptionD: {
        //认购单弹窗
        trunoff: false
      },
      SubscriptionArr: null, //认购单选择后的数据
      // 认购单
      //开票金额片段
      valNum: {
        sum: "",
        off: false
      },
      // 替换的
      fromData: {
        projectId: "",
        standUid: ""
      },
      totol: 0 //开票总金额
    };
  },
  created() {},
  methods: {
    handleClose(done) {
      this.resetForm();
      this.SubscriptionArr = null;
      this.dialogVisible.trunoff = false;
    },
    // 保存
    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          let data = JSON.parse(JSON.stringify(this.formInline));
          data.projectId = this.fromData.projectId;
          data.standUid = this.fromData.standUid;
          if (
            this.SubscriptionArr == null ||
            this.SubscriptionArr.length == 0
          ) {
            return this.$message.error("请选择订购单");
          }
          // data.standUid = this.selectItemstandUid.uid;
          // data.projectId = this.selectItem.projectId;

          let trunoff = false;
          let details = this.SubscriptionArr.map(item => {
            let obj = {};
            if (item.billMoney == "" || item.inputCanCommission == "") {
              trunoff = true;
            }
            obj.ticketId = item.ticketId;
            obj.billMoney = item.billMoney;
            obj.canCommission = item.inputCanCommission;
            return obj;
          });
          if (trunoff) return this.$message.error("请填写金额");

          // editInvoice()
          editInvoice({
            ...data,
            details: details,
            invoiceId: this.$parent.invoiceId
          }).then(res => {
            if (res.data.code === "000") {
              this.$confirm("是否提交?", "提示", {
                confirmButtonText: "是",
                cancelButtonText: "否",
                type: "warning"
              })
                .then(() => {
                  // 提交
                  commitInvoice({ invoiceId: this.$parent.invoiceId }).then(
                    res => {
                      if (res.data.code === "000") {
                        this.$message({
                          type: "success",
                          message: "提交成功!"
                        });
                      }
                      this.$parent.onSubmit();
                      this.resetForm();
                    }
                  );
                })
                .catch(() => {
                  this.$message({
                    type: "info",
                    message: "已编辑"
                  });
                  this.$parent.onSubmit();
                  this.resetForm();
                });
            }
          });

          // 保存
          // addInvoice({ ...data, details: details }).then(res => {
          //   if (res.data.code === "000") {
          //     this.$confirm("是否提交?", "提示", {
          //       confirmButtonText: "确定",
          //       cancelButtonText: "取消",
          //       type: "warning"
          //     })
          //       .then(() => {
          //         // 提交
          //         commitInvoice({ invoiceId: res.data.bizData.invoiceId }).then(
          //           res => {
          //             if (res.data.code === "000") {
          //               this.$message({
          //                 type: "success",
          //                 message: "提交成功!"
          //               });
          //             }
          //           this.$parent.onSubmit()
          //           }
          //         );
          //       })
          //       .catch(() => {
          //         this.$message({
          //           type: "info",
          //           message: "已保存"
          //         });
          //         this.$parent.onSubmit()
          //       });
          //   }
          // });
        } else {
          return false;
        }
      });
    },
    // 添加
    addFn() {
      if (
        this.formInline.projectId != null &&
        this.formInline.projectId != ""
      ) {
        this.$refs.Subscription.selectTicketList(this.selectItem.projectId);
        if (this.SubscriptionArr != null && this.SubscriptionArr.length != 0) {
          this.$refs.Subscription.multipleSelection = this.SubscriptionArr;
        }
        this.SubscriptionD.trunoff = true;
      } else {
        this.$message.error("请选择项目");
      }
    },
    todutyNo() {
      this.dataDotoDy.trunoff = true;
      this.$refs.toDycard.searchFn();
    },
    async standUidSearch(queryString, cb) {
      this.selectstandUid.key = queryString;
      await this.requestSearchTwo(cb);
    },
    handleSelectstandUid(val) {
      this.formInline.standUid = val.uid;
      this.selectItemstandUid = val;
    },
    keydownFnss() {
      if (this.values.length === 0) {
        this.formInline.standUid = "";
        this.selectstandUid.key = "";
      }
    },
    requestSearchTwo(cb) {
      this.selectstandUid.deptId = this.$store.getters.userinfo.departmentId;
      userList(this.selectstandUid).then(res => {
        // console.log(res)
        if (res.data.code === "000") {
          if (res.data.bizData.items.length === 0) {
            return cb([{}]);
          }
          this.standUidArr = res.data.bizData.items.map(item => {
            item.value = item.name;
            return item;
          });
          var restaurants = this.standUidArr;
          return cb(restaurants);
        }
      });
    },
    //搜索事件
    hhghjgl(item) {
      this.formInline.projectId = item.projectName;
      this.formInline.type = item.type;
      this.formInline.recCompanyId = item.recCompanyId;
      // this.formInline.standUid = item.standName;
      this.values = item.standName;
      this.formInline.standUid=
      this.formInline.dutyNo = item.dutyNo;
      this.fromData.standUid = item.standUid;
      this.fromData.projectId = item.projectId;
      this.formInline.remark = item.addRemark;
      this.SubscriptionArr = item.details;
      this.selectItem = item;
      this.dataDotoDy.projectName = item.projectName;
      this.formInline.dutyNo = item.dutyNo;
      this.dataDotoDy.dutyNo = item.dutyNo;
      this.dataDotoDy.projectId = item.projectId;
    },
    // 重置
    resetForm() {
      this.$refs.ruleForm.resetFields();
      this.selectItem = null;
      this.selectItemstandUid = null;
      this.SubscriptionArr = null;
      this.dialogVisible.trunoff = false;
      this.$parent.handleClick();
    },
    // 认购单选择
    handleSelectionChange(val) {
      this.SubscriptionArr = JSON.parse(JSON.stringify(val)).map(item => {
        item.billMoney = "";
        item.inputCanCommission = "";
        return item;
      });
    },

    changeStandUid(val) {
      this.fromData.standUid = val;
    },
    // //////////////
    //删除
    ClickDelet(val) {
      this.SubscriptionArr.forEach((item, index) => {
        if (item.departmentId === val.departmentId) {
          this.SubscriptionArr.splice(index, 1);
        }
      });
    }
  },
  computed: {
    tableDataFn() {
      if (this.SubscriptionArr == null || this.SubscriptionArr.length == 0) {
        return false;
      }
      return this.SubscriptionArr;
    },
    prjiec() {
      return this.dataSearch.some(item => {
        if (item.projectId === this.formInline.projectId) {
          this.formInline.dutyNo = item.dutyNo;
          this.selectItem = item;
          this.dataDotoDy.projectName = item.projectName;
          this.formInline.dutyNo = item.dutyNo;
          this.dataDotoDy.dutyNo = item.dutyNo;
          this.dataDotoDy.projectId = item.projectId;
          return true;
        }
      });
    },
    // 开票金额
    totolFn() {
      let num = 0;
      if (this.SubscriptionArr == null || this.SubscriptionArr.length == 0) {
        return 0;
      }
      this.SubscriptionArr.forEach(item => {
        item.billMoney == "" ? 0 : Number(item.billMoney);
        item.inputCanCommission=item.inputCanCommission == "" ? 0 : Number(item.inputCanCommission);
        num += Number(item.billMoney);
        item.inputCanCommission > item.billMoney
          ? this.$message.error("可结不能超过开票金额") &&
            (item.inputCanCommission = item.billMoney)
          : "";
      });
      return num;
    }
  }
};
</script>
<style lang="scss" scoped>
.addPro {
  position: absolute;
  .btn {
    position: absolute;
    top: 20px;
    right: 50px;
  }
  .addIcon {
    font-size: 22px;
    font-size: 22px;
    margin-top: 8px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding: 10px 0;
    font-family: SimSun;
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding: 10px 0;
  }
  .dialog-footer {
    // position: absolute;
    // width: 100%;
    // bottom: 0;
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    /deep/.el-card__body {
      display: flex;
      align-items: center;
      padding: 0;
      font-size: 18px;
      .saveColor {
        border-radius: 0;
        width: 160px;
        margin-left: 20px;
      }
    }
  }
}
.addPro /deep/.el-dialog {
  background-color: rgb(245, 241, 241);
  .el-dialog__body {
    padding-top: 0;
    padding-bottom: 1px;
  }
}
</style>