<template>
  <div class="commission">
    <!--title 收佣管理 -->
    <template v-if="!InvoiceOff">
      <!-- 查询 -->
      <el-card>
        <el-form :inline="true" :model="queryfrom" class="me_from demo-form-inline">
          <el-form-item>
            <el-select style="width:190px;" v-model="queryfrom.projectId" filterable placeholder="请选择项目" >
              <el-option
                v-for="(item,index) in selctAll"
                :key="item.projectId+index+item.projectName"
                :label="item.projectName"
                :value="item.projectId"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select style="width:190px;" v-model="queryfrom.status" placeholder="请选择状态">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="已保存" :value="1"></el-option>
              <el-option label="已提交" :value="2"></el-option>
              <el-option label="已审核" :value="3"></el-option>
              <el-option label="已开票" :value="5"></el-option>
              <el-option label="已收佣" :value="6"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-date-picker
              style="width:290px;"
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="选择开始时间"
              end-placeholder="选择结束时间"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-input style="width:190px;" placeholder="项目名或客户名和电话"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="background:#0b8d7e;border:none;" @click="onSubmit">搜索</el-button>
            <el-button type="primary" style="background:#f1420ef7;border:none;" @click="AddPro">添加发票</el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <!-- 查询 -->
      <!-- 表格 -->
      <el-card style="margin-top:20px;">
        <header style="font-weight: 800;padding-bottom: 8px;color: #000;">收佣列表</header>
        <el-table
          ref="table"
          height="410"
          :cell-style="cellStyle"
          :header-cell-style="{background:'#edfaf8',color:'#333333'}"
          :row-class-name="tableRowClassName"
          :data="tableData"
          border
          style="width: 100%"
        >
          <el-table-column prop="projectName" label="项目" width="180"></el-table-column>
          <el-table-column prop="time" label="日期" width="180"></el-table-column>
          <el-table-column prop="standName" label="对佣人"></el-table-column>
          <el-table-column prop="money" label="开票总金额">
            <template slot-scope="scope">{{scope.row.money}}元</template>
          </el-table-column>
          <template v-if="this.queryfrom.status===3">
            <el-table-column prop="status" label="状态">
              <template slot-scope="scope">{{scope.row.status===3?'审核通过':'审核拒绝'}}</template>
            </el-table-column>
          </template>
          <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small">开票详情</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <Pagination
          @handleSizeChange="handleSizeChange"
          @handleCurrentChange="handleCurrentChange"
          :paginationData="paginationData"
        />
      </el-card>
    </template>
    <!-- 弹窗 -->

    <addPro ref="addPros" :dialogVisible="dialogVisible" />
    <eidt ref="eidts" :dialogVisible="dialogVisibleeidt" />
    <!-- 开票详情 -->
    <idetails
      @InvoiceOffFN="InvoiceOff=false"
      :InvoiceInfo="InvoiceInfo"
      :InvoiceData="InvoiceData"
      v-if="InvoiceOff"
    />
    <!-- 开票详情 -->
  </div>
</template>
<script>
import {
  invoiceLog,
  simpleInvoiceList,
  allDynaList,
  invoiceDetail,
  checkInvoice
} from "@/api/business/commission";
import idetails from "components/business/commission/idetails";
import eidt from "components/business/commission/edit";
import addPro from "components/business/commission/addPro";
import { dateTime } from "@/utils/common";
import myMixin from "@/mixin/index";
export default {
  mixins: [myMixin],
  components: {
    idetails,
    addPro,
    eidt
  },
  data() {
    return {
      queryfrom: {
        //查询
        status: 0,
        projectId: 0,
        pageNo: 1,
        pageSize: 10,
        beginTime:'',
        endTime:''
      },
      selectall: {
        //项目下拉接口参数查询
        //   projectId:"",
        pageNo: 1,
        pageSize: 6
      },
      selctAll: [], //项目菜单下拉
      tableData: [], //数据源
      dialogVisible: {
        //新增控制弹窗
        trunoff: false,
        projectAll: []
      },
      dialogVisibleeidt: {
        //编辑控制弹窗
        trunoff: false,
        projectAll: []
      },
      off: false,
      paginationData: {
        //分页
        pageSize: 10,
        currentPage: 1,
        total: 1000
      },
      invoiceId: null,
      status: null,
      // 开票详情操作
      InvoiceOff: false, //开票详情
      InvoiceData: null, //详情数据
      InvoiceInfo: null, //日志
      // 开票详情操作
      slectInvoice: null,
      /////新增
      value1: null
    };
  },
  updated() {
    if (this.$refs["table"] == undefined) {
      return;
    }
    this.$refs["table"].doLayout();
  },
  methods: {
    //查询
    onSubmit() {
      // this.dialogVisibleeidt.trunoff = false;
      // this.dialogVisible.trunoff = false;
      // this.InvoiceOff = false;
      this.queryfrom.pageSize=10
      this.queryfrom.pageNo=1
      this.requetList();
    },
    requetList() {
      simpleInvoiceList(this.queryfrom).then(res => {
        if (res.data.code === "000") {
          this.tableData =
            res.data.bizData.items.length === 0
              ? []
              : res.data.bizData.items.map(item => {
                  item.time = dateTime(item.time);
                  return item;
                });
                this.paginationData.total=res.data.bizData.total
        }
      });
    },
    // 新增
    AddPro() {
      this.dialogVisible.trunoff = true;
    },
    //编辑
    eidtPro() {
      this.dialogVisibleeidt.trunoff = true;
    },
    //项目菜单请求
    selectFn() {
      allDynaList(this.selectall)
        .then(res => {
          if (res.data.code === "000") {
            this.selctAll = res.data.bizData.items;
            this.selctAll.unshift({
              mdfDate: 1565602743000,
              projectId: 0,
              projectName: "全部"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //一页显示的数目大小
    handleSizeChange(val) {
      this.queryfrom.pageSize = val;
      this.paginationData.pageSize = val;
      this.requetList();
    },
    //第几页
    handleCurrentChange(val) {
      this.queryfrom.pageNo = val;
      this.paginationData.currentPage = val;
      this.requetList();
    },
    // 开票详情
    handleClick(val) {
      if (val == "" || val == null) {
        val = this.slectInvoice;
      }
      this.InvoiceOff = true;

      this.invoiceId = val.invoiceId;
      this.slectInvoice = val;
      invoiceDetail({ invoiceId: val.invoiceId }).then(res => {
        if (res.data.code === "000") {
          this.InvoiceData = res.data.bizData;
          this.status = this.InvoiceData.status;
        }
      });
      invoiceLog({ invoiceId: val.invoiceId }).then(res => {
        if (res.data.code === "000") {
          this.InvoiceInfo = res.data.bizData.items.map(item => {
            item.color = "#0bbd87";
            item.opTime = dateTime(item.opTime);
            return item;
          });
        }
      });
    }
  },
  created() {
    this.selectFn();
    this.requetList();
  },
  watch:{
    value1:{
      handler(){
        if(this.value1==null){
           this.queryfrom.beginTime=''
           this.queryfrom.endTime=''
           return true;
        }
        this.queryfrom.beginTime=dateTime(this.value1[0])
        this.queryfrom.endTime=dateTime(this.value1[1])
      }
    }
  }
};
</script>
<style lang="scss">
.commission {
  .me_from {
    .el-form-item {
      margin-bottom: 6px;
    }
  }
}
</style>